.contact-form {
  display: flex;
  flex-direction: column;
}

.contact-form input,
.contact-form textarea {
  margin-bottom: 1rem;
  border: 1px #333 solid;
  border-radius: 10px;
  padding: 0.2rem 0.5rem;
}

.contact-form a {
  align-self: flex-end;
  border: 1px #007bff solid;
  border-radius: 10px;
  padding: 0.2rem 0.5rem;
}
