/* DESKTOP JOIN MODAL */

#join-modal-overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 10;
  background-color: rgba(0, 0, 0, 0.3);
}

#join-modal {
  width: 50% !important;
  min-height: 50%;
  background-color: white;
  border-radius: 10px;
  padding: 1rem;
}

/*  MOBILE JOIN OVERLAY */

#join-overlay-exit:hover {
  cursor: pointer;
}

#join-overlay {
  position: fixed;
  top: 0;
  right: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 0vw;
  height: 100vh;
  z-index: 10;
  overflow-x: hidden;
  transition: width 0.5s;
  box-shadow: -2px 5px 6px #00000016;
}

#join-overlay-exit {
  color: white;
  position: absolute;
  top: 0;
  right: 0;
  margin-top: 20px;
  margin-right: 15px;
}

#join-overlay-exit.modal {
  color: #c95858;
}

#join-overlay div {
  text-align: left;
  margin-right: 15px;
  margin-left: 15px;
  color: white;
}
