.mrs-eaves.bold {
  font-family: mrs-eaves-xl-serif, serif;
  font-weight: 700;
  font-style: normal;
}

.mrs-eaves.heavy {
  font-family: mrs-eaves-xl-serif, serif;
  font-weight: 800;
  font-style: normal;
}

.mr-eaves {
  font-family: mr-eaves-xl-sans, sans-serif;
  font-weight: 300;
  font-style: normal;
}

.mr-eaves.italic {
  font-family: mr-eaves-xl-sans, sans-serif;
  font-weight: 300;
  font-style: italic;
}

header.mobile-size {
  font-size: 90%;
}

.paragraph.mr-eaves {
  line-height: 1.1rem;
}

.paragraph.mobile-size {
  font-size: 0.8rem;
}

.header {
  font-size: 1.75rem;
  text-transform: uppercase;
  letter-spacing: 0.075rem;
}

.header.small {
  font-size: 1.25rem;
}
