#navbar {
  display: flex;
  justify-content: space-between;
  align-items: center;
  box-shadow: 0 5px 6px #00000016;
  padding: 0px 10px;
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  z-index: 10;
  background-color: whitesmoke;
}

#navbar a {
  margin-right: 1rem;
  color: #343a40;
}

#home-link:hover {
  text-decoration: none;
}

#navbar.mrs-eaves,
#navbar .btn {
  text-transform: uppercase;
  font-size: 0.9rem;
}

.navbar-logo {
  max-width: 50px;
  height: auto;
}

#nuEA-title {
  font-size: 1.1rem;
  line-height: 1.3rem;
  text-decoration: none;
}

#nuEA-title.mobile-size {
  line-height: 1rem;
  font-size: 0.8rem;
}

.menu-button:hover,
#navigation-overlay-exit:hover {
  cursor: pointer;
}

#navigation-overlay {
  position: fixed;
  top: 0;
  right: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 0vw;
  height: 100vh;
  z-index: 10;
  overflow-x: hidden;
  transition: width 0.5s;
  box-shadow: -2px 5px 6px #00000016;
}

#navigation-overlay-exit {
  color: white;
  position: absolute;
  top: 0;
  right: 0;
  margin-top: 20px;
  margin-right: 15px;
}

#navigation-overlay div {
  text-align: center;
  margin-right: 15px;
  margin-left: 15px;
}

#navigation-overlay div a {
  color: white;
}

#join-button-footer {
  position: absolute;
  bottom: 0;
  left: 0;
  position: fixed;
  width: 100vw;
  display: flex;
  justify-content: flex-end;
  padding-right: 15px;
  padding-bottom: 20px;
  z-index: 1;
}
